.header {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    overflow: hidden;
    background-color: rgba(182, 208, 226, 0.5);
    padding: 20px 10px;
    margin-bottom: 10px;
}

/* Style the header links */
.header a {
    float: left;
    color: black;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 4px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.header a.logo img {
    position: absolute;
    top: -25px;
    left: 10px;
    width: 150px;
    border-radius: 50%;
}

/* Change the background color on mouse-over */
.header a:hover {
    background-color: green;
    color: white;
}

/* Style the active/current link*/
.header a.active {
    background-color: dodgerblue;
    color: white;
}

/* Float the link section to the right */
.headerRight {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-left: 150px;
    margin-right: 15px;
}

.logout {
    color: black;
    margin: 0 15px 0 0;
    text-align: center;
    font-size: 10px;
    font-weight: bold;
    border: none;
    border-radius: 50px;
    cursor: pointer;

    background: linear-gradient(233deg, #09f0b5, #04e3f6, #f4305d, #3a30f4, #f430bc);
    background-size: 1000% 1000%;

    -webkit-animation: AnimationName 18s ease infinite;
    -moz-animation: AnimationName 18s ease infinite;
    animation: AnimationName 18s ease infinite;
}

@-webkit-keyframes AnimationName {
    0% {
        background-position: 0% 96%;
    }
    50% {
        background-position: 100% 5%;
    }
    100% {
        background-position: 0% 96%;
    }
}
@-moz-keyframes AnimationName {
    0% {
        background-position: 0% 96%;
    }
    50% {
        background-position: 100% 5%;
    }
    100% {
        background-position: 0% 96%;
    }
}
@keyframes AnimationName {
    0% {
        background-position: 0% 96%;
    }
    50% {
        background-position: 100% 5%;
    }
    100% {
        background-position: 0% 96%;
    }
}

/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */
@media (max-width: 500px) {
    .header a {
        float: none;
        display: block;
        text-align: left;
    }
    .header-right {
        float: none;
    }
}
